@charset "utf-8";

header{
    display: flex;
    width:100%;
    height:156px;
}
.header_inner{
    display:flex;
    padding-top:37px;
    margin:0 auto;
}
.header_inner input{
    margin:0 auto;
    width:400px;height:42px;
    border-radius: 6px;
}
.product{
    width:1050px;
    margin: 0 auto;
}
.product h3{
    font-size:26px;
    text-align:center;
    padding:30px 0px;
}
.product_list{
    padding:30px;
    justify-content:space-around;
    display:flex;
}
.product_list li img{
    width:249px;
}
.product_list p{
    line-height:30px;
}
.price{
    font-weight:bold;
}
.banner{
    margin:80px 0px;
}
.banner img{
    width:100%;
}
.inner{
    width:1050px;
    margin: 0px auto;
}
footer{
    padding: 20px 10px; 
    height:580px;
    border:1px solid #ccc;
}
.footer_inner{
    width:1050px;
    margin: 0 auto;
}
.footer_inner h1{
    padding-bottom: 17px;
    font-size: 20px;
    line-height: 29px;
}
.footer_inner div{
    display:block;
    padding-bottom: 17px;
    font-size: 28px;
    line-height: 40px;
}
.footer_inner p{
    display: flex;
    width: 100%;
    padding-bottom: 29px;
}
.footer_inner div input{
    display: block;
    width: 140px;
    height: 40px;
    border: 1px solid rgb(226, 226, 226);
    border-radius: 3px;
    line-height: 39px;
    text-align: center;
    margin-right: 16px;
    margin-bottom: 16px;
}